'use client';

import { useState, useEffect } from 'react';
import { AppLayout } from '@/components/layout/AppLayout';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Input } from '@/components/ui/Input';
import { Alert } from '@/components/ui/Alert';
import { useAuth } from '@/components/auth/SessionManager';
import { 
  FileText, 
  Download, 
  Share2, 
  Search, 
  Filter,
  Calendar,
  BarChart3,
  TrendingUp,
  Eye,
  Star,
  Clock,
  Target,
  Award
} from 'lucide-react';

interface InterviewReport {
  id: string;
  sessionId: string;
  date: Date;
  role: string;
  company: string;
  type: 'behavioral' | 'technical' | 'mixed';
  difficulty: 'beginner' | 'intermediate' | 'advanced';
  duration: number; // in minutes
  overallScore: number;
  scores: {
    confidence: number;
    clarity: number;
    relevance: number;
    structure: number;
  };
  questionsAnswered: number;
  status: 'completed' | 'partial';
  hasDetailedAnalysis: boolean;
}

// Empty reports array for new users
const MOCK_REPORTS: InterviewReport[] = [];

export default function ReportsPage() {
  const { user, isAuthenticated } = useAuth();
  const [reports, setReports] = useState<InterviewReport[]>([]);
  const [filteredReports, setFilteredReports] = useState<InterviewReport[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterType, setFilterType] = useState<'all' | 'behavioral' | 'technical' | 'mixed'>('all');
  const [filterStatus, setFilterStatus] = useState<'all' | 'completed' | 'partial'>('all');
  const [sortBy, setSortBy] = useState<'date' | 'score' | 'role'>('date');
  const [loading, setLoading] = useState(true);

  // Load reports on component mount
  useEffect(() => {
    const loadReports = async () => {
      setLoading(true);
      try {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));
        setReports(MOCK_REPORTS); // Empty array for new users
        setFilteredReports(MOCK_REPORTS);
      } catch (error) {
        console.error('Failed to load reports:', error);
      } finally {
        setLoading(false);
      }
    };

    if (isAuthenticated) {
      loadReports();
    }
  }, [isAuthenticated]);

  // Apply filters and search
  useEffect(() => {
    let filtered = [...reports];

    // Apply search filter
    if (searchTerm) {
      filtered = filtered.filter(report => 
        report.role.toLowerCase().includes(searchTerm.toLowerCase()) ||
        report.company.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Apply type filter
    if (filterType !== 'all') {
      filtered = filtered.filter(report => report.type === filterType);
    }

    // Apply status filter
    if (filterStatus !== 'all') {
      filtered = filtered.filter(report => report.status === filterStatus);
    }

    // Apply sorting
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'date':
          return b.date.getTime() - a.date.getTime();
        case 'score':
          return b.overallScore - a.overallScore;
        case 'role':
          return a.role.localeCompare(b.role);
        default:
          return 0;
      }
    });

    setFilteredReports(filtered);
  }, [reports, searchTerm, filterType, filterStatus, sortBy]);

  const handleViewReport = (reportId: string) => {
    // Navigate to detailed report view
    window.location.href = `/reports/${reportId}`;
  };

  const handleExportReport = (report: InterviewReport) => {
    // Export individual report
    const exportData = {
      report,
      exportedAt: new Date().toISOString(),
      exportedBy: user?.email
    };
    
    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `interview-report-${report.id}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleExportAll = () => {
    // Export all reports
    const exportData = {
      reports: filteredReports,
      summary: {
        totalReports: filteredReports.length,
        averageScore: filteredReports.reduce((sum, r) => sum + r.overallScore, 0) / filteredReports.length,
        completedSessions: filteredReports.filter(r => r.status === 'completed').length
      },
      exportedAt: new Date().toISOString(),
      exportedBy: user?.email
    };
    
    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `all-interview-reports-${new Date().toISOString().split('T')[0]}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const getScoreColor = (score: number): string => {
    if (score >= 80) return 'text-green-600';
    if (score >= 60) return 'text-yellow-600';
    return 'text-red-600';
  };

  const getScoreBackground = (score: number): string => {
    if (score >= 80) return 'bg-green-100';
    if (score >= 60) return 'bg-yellow-100';
    return 'bg-red-100';
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'behavioral': return <Star className="w-4 h-4" />;
      case 'technical': return <BarChart3 className="w-4 h-4" />;
      case 'mixed': return <TrendingUp className="w-4 h-4" />;
      default: return <FileText className="w-4 h-4" />;
    }
  };

  if (!isAuthenticated) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="warning" title="Authentication Required">
            Please sign in to access your interview reports.
          </Alert>
        </div>
      </AppLayout>
    );
  }

  return (
    <AppLayout>
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="max-w-7xl mx-auto px-6">
          {/* Header */}
          <div className="mb-8">
            <div className="flex items-center justify-between">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Interview Reports</h1>
                <p className="text-gray-900 mt-2">
                  Review your interview performance and track your progress over time
                </p>
              </div>
              <div className="flex space-x-3">
                <Button
                  variant="secondary"
                  onClick={handleExportAll}
                  className="flex items-center"
                  disabled={filteredReports.length === 0}
                >
                  <Download className="w-4 h-4 mr-2" />
                  Export All
                </Button>
                <Button
                  onClick={() => window.location.href = '/progress'}
                  className="flex items-center"
                >
                  <TrendingUp className="w-4 h-4 mr-2" />
                  View Progress
                </Button>
              </div>
            </div>
          </div>

          {/* Summary Stats */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            <Card className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-blue-100 rounded-lg">
                  <FileText className="w-6 h-6 text-blue-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-800">Total Reports</p>
                  <p className="text-2xl font-bold text-gray-900">{reports.length}</p>
                </div>
              </div>
            </Card>

            <Card className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-green-100 rounded-lg">
                  <Target className="w-6 h-6 text-green-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-800">Average Score</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {reports.length > 0 
                      ? Math.round(reports.reduce((sum, r) => sum + r.overallScore, 0) / reports.length)
                      : 0}%
                  </p>
                </div>
              </div>
            </Card>

            <Card className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-purple-100 rounded-lg">
                  <Clock className="w-6 h-6 text-purple-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-800">Total Time</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {Math.round(reports.reduce((sum, r) => sum + r.duration, 0) / 60)}h
                  </p>
                </div>
              </div>
            </Card>

            <Card className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-yellow-100 rounded-lg">
                  <Award className="w-6 h-6 text-yellow-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-800">Completed</p>
                  <p className="text-2xl font-bold text-gray-900">
                    {reports.filter(r => r.status === 'completed').length}
                  </p>
                </div>
              </div>
            </Card>
          </div>

          {/* Filters and Search */}
          <Card className="p-6 mb-8">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <div>
                <Input
                  placeholder="Search by role or company..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full"
                />
              </div>
              
              <div>
                <select
                  value={filterType}
                  onChange={(e) => setFilterType(e.target.value as any)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Types</option>
                  <option value="behavioral">Behavioral</option>
                  <option value="technical">Technical</option>
                  <option value="mixed">Mixed</option>
                </select>
              </div>
              
              <div>
                <select
                  value={filterStatus}
                  onChange={(e) => setFilterStatus(e.target.value as any)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="all">All Status</option>
                  <option value="completed">Completed</option>
                  <option value="partial">Partial</option>
                </select>
              </div>
              
              <div>
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value as any)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="date">Sort by Date</option>
                  <option value="score">Sort by Score</option>
                  <option value="role">Sort by Role</option>
                </select>
              </div>
            </div>
          </Card>

          {/* Reports List */}
          {loading ? (
            <div className="flex items-center justify-center py-12">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
              <span className="ml-3 text-gray-600">Loading reports...</span>
            </div>
          ) : filteredReports.length === 0 ? (
            <Card className="p-12 text-center">
              <FileText className="w-12 h-12 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Reports Found</h3>
              <p className="text-gray-600 mb-6">
                {reports.length === 0 
                  ? "You haven't completed any interviews yet. Start practicing to see your reports here."
                  : "No reports match your current filters. Try adjusting your search criteria."
                }
              </p>
              {reports.length === 0 && (
                <Button onClick={() => window.location.href = '/interview'}>
                  Start Your First Interview
                </Button>
              )}
            </Card>
          ) : (
            <div className="space-y-4">
              {filteredReports.map((report) => (
                <Card key={report.id} className="p-6 hover:shadow-lg transition-shadow">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-4">
                      <div className={`p-3 rounded-lg ${getScoreBackground(report.overallScore)}`}>
                        {getTypeIcon(report.type)}
                      </div>
                      
                      <div className="flex-1">
                        <div className="flex items-center space-x-3 mb-2">
                          <h3 className="text-lg font-semibold text-gray-900">
                            {report.role} at {report.company}
                          </h3>
                          <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                            report.status === 'completed' 
                              ? 'bg-green-100 text-green-800' 
                              : 'bg-yellow-100 text-yellow-800'
                          }`}>
                            {report.status}
                          </span>
                        </div>
                        
                        <div className="flex items-center space-x-6 text-sm text-gray-600">
                          <span className="flex items-center">
                            <Calendar className="w-4 h-4 mr-1" />
                            {report.date.toLocaleDateString()}
                          </span>
                          <span className="flex items-center">
                            <Clock className="w-4 h-4 mr-1" />
                            {report.duration} min
                          </span>
                          <span className="capitalize">{report.type}</span>
                          <span className="capitalize">{report.difficulty}</span>
                          <span>{report.questionsAnswered} questions</span>
                        </div>
                      </div>
                    </div>
                    
                    <div className="flex items-center space-x-4">
                      {report.status === 'completed' && (
                        <div className="text-right">
                          <div className={`text-2xl font-bold ${getScoreColor(report.overallScore)}`}>
                            {report.overallScore}%
                          </div>
                          <div className="text-xs text-gray-500">Overall Score</div>
                        </div>
                      )}
                      
                      <div className="flex space-x-2">
                        <Button
                          variant="secondary"
                          size="sm"
                          onClick={() => handleViewReport(report.id)}
                          className="flex items-center"
                        >
                          <Eye className="w-4 h-4 mr-1" />
                          View
                        </Button>
                        
                        <Button
                          variant="secondary"
                          size="sm"
                          onClick={() => handleExportReport(report)}
                          className="flex items-center"
                        >
                          <Download className="w-4 h-4 mr-1" />
                          Export
                        </Button>
                      </div>
                    </div>
                  </div>
                  
                  {report.status === 'completed' && (
                    <div className="mt-4 pt-4 border-t border-gray-200">
                      <div className="grid grid-cols-4 gap-4">
                        {Object.entries(report.scores).map(([key, score]) => (
                          <div key={key} className="text-center">
                            <div className={`text-lg font-semibold ${getScoreColor(score)}`}>
                              {score}%
                            </div>
                            <div className="text-xs text-gray-500 capitalize">
                              {key}
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </Card>
              ))}
            </div>
          )}
        </div>
      </div>
    </AppLayout>
  );
}